import React from 'react';
import styled from '@emotion/styled';
import { css } from '@emotion/core';
import { Link, graphql, useStaticQuery } from 'gatsby';
import Image from 'gatsby-image';


const NavLink = styled(Link)`
  color: #222;
  font-size: 1rem;
  font-weight: ${props => props.fontWeight || 'normal'};
  line-height: 1;
  margin: 0 0.5rem 0 0;
  padding: 0.25rem;
  text-decoration: none;

  &.current-page {
    border-bottom: 2px solid #222;
  }

  &:last-of-type {
    margin-right: 0;
  }
`;


const Header = () => {
  function toggle() {
    document.querySelector('.header-nav-links').classList.toggle('open');
  }

  const { image } = useStaticQuery(graphql`
    query {
      image: file(relativePath: { eq: "nwwnc-2019-logo-hi-res-cyan.png" }) {
        sharp: childImageSharp {
          fluid(maxWidth: 120, maxHeight: 120) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);


  return (
    <header
      css={css`
        background: #eee;
        border-bottom: 1px solid #ddd;
        display: flex;
        justify-content: space-between;
        padding: 0.5rem calc((100vw - 675px - 0.5rem) / 2);
        align-items: top;

        .toggle {
          cursor: pointer;
        }

        .header-nav-links {
          // Links initially are not visible
          display: none;
          // When links are visible on smaller screens, they show up vertically in the dropdown
          flex-direction: column;
          margin-right: 1rem;
        }

        // When "open" class is added after clicking, the links will be shown using flexbox
        .header-nav-links.open {
          display: flex;
        }

        @media (min-width: 500px) {

          align-items: center;

          .toggle {
            // If the screen width is wider, the toggle icon isn't visible
            display: none;
          }

          .header-nav-links {
            display: flex;
            // For wider screens, the links will be in a row with even space between each
            flex-direction: row;
            justify-content: space-evenly;
          }

        }

        label {
          font-weight: bold;
          display: inline-block;
          color: black;
          font-family: Palatino, Palatino Linotype, Palatino LT STD, Book Antiqua, Georgia, serif;
        }

        button {
          margin-top: 0;
          margin-right: 1rem;
          padding: 0.25rem 0.5rem;
          height: 50px;
          border: 1px solid #ccc;
          background-image: linear-gradient(to top, #ddbbffdd, #eeeeee00);
          background-color: #eee;
          border-radius: 0;
        }
        * {
          margin-top: 0;
        }

      `}
    >
      <div css={css`
        margin-left: 1rem;
        display: flex;
        align-items: center;
        @media (max-width: 500px) {
          flex-direction: column;
        }

        `}>
        <Image
          fluid={image.sharp.fluid}
          css={css`
            width: 40px;
            margin-right: 0.25rem;
          `}
          alt="North Westwood Neighborhood Council"
        />
        <label>NWWNC</label>
      </div>
      <nav
        css={css`
          margin-top: 0;
        `}
        className="header-nav-links"
      >
        <NavLink to="/" activeClassName="current-page">
          Home
        </NavLink>
        <NavLink to="/meetings" onClick={toggle} activeClassName="current-page">
          Meetings
        </NavLink>
        <NavLink to="/minutes" onClick={toggle} activeClassName="current-page">
          Minutes
        </NavLink>
        <NavLink to="/upcoming" onClick={toggle} activeClassName="current-page">
          Upcoming
        </NavLink>
      </nav>
      <button className="toggle" onClick={toggle}><img alt="Menu Button Toggle" src="data:image/svg+xml;base64," /></button>
    </header>
  )
};

export default Header;
